import React, { useRef, useEffect, useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import "../../css/Main.css";
import MainSlider from "../slider/MainSlider";
import MainSlider2 from "../slider/MainSlider2";

import { gsap } from "gsap";
import { useNavigate } from "react-router-dom";
import FestivalCard from "../festival/FestivalCard";
import Carousel from "react-bootstrap/Carousel";

function Main(props) {
  let headlineText = useRef(null);
  let startButton = useRef(null);
  let eventButton = useRef(null);

  const navigate = useNavigate();

  const [post, setPost] = useState([]);

  const FilterCard = () => {
    return props.data;
  };

  useEffect(() => {
    setPost([...FilterCard()]);
  }, [props.category]);

  useEffect(() => {
    gsap.to(headlineText, 0.8, {
      opacity: 1,
      y: -30,
      ease: "Power3.out",
      delay: 0.2,
    });
    gsap.to(startButton, 0.8, {
      opacity: 1,
      y: -30,
      ease: "Power3.out",
      delay: 0.9,
    });
  }, []);

  function handleStartClick() {
    navigate("/running");
  }
  function handleEventClick() {
    navigate("/event");
  }

  return (
    <div className="container">
      <div className="main">
        <MainSlider />
        <div className="mainBox">
          <div className="mainInfo_text">
            <span>
               {" "}
              <span style={{ color: "#49e594", fontWeight: "bold" }}>
                 
              </span>
                
              <br style={{ border: "1px solid black" }} />
              <br />
                <span style={{ fontWeight: "bold" }}></span>
              
              <br />
               <span style={{ fontWeight: "bold" }}></span>
               <br />
               {" "}
              <span style={{ fontWeight: "bold" }}> </span> <br />
              <br />
              <span style={{ fontWeight: "bold" }}>  </span>
            </span>
          </div>
        </div>
        <div
          style={{
            marginTop: "20px",
           
            width: "100%",
          }}
        />
        <div />

        <div className="mainBox2">
          <div className="headline1">
            <h3 className="headline_text">
              <span style={{ color: "#49e594" }}></span>
              <br />
              <span style={{ color: "#49e594" }}></span>
            </h3>
          </div>
        </div>

        <Col md={12} lg={12}>
          <Container>
            <Row className="row gx-0">
              {post.map((item, i) =>
                i < 1 ? <FestivalCard data={item} i={i} key={item.id} /> : null
              )}
            </Row>
          </Container>
        </Col>

        <div
          className="eventButton"
          onClick={handleEventClick}
          ref={(el) => {
            eventButton = el;
          }}
        >
          <span className="eventButtonText"></span>
        </div>
        <div  />
        <div />

        <div className="headline2">
          <h3
            className="headline_text"
            ref={(el) => {
              headlineText = el;
            }}
          >
            <span style={{ color: "#49e594" }}></span>
            <br />
            <span style={{ color: "#49e594" }}></span>
          </h3>
          <div
          className="startButton"
          onClick={handleStartClick}
          ref={(el) => {
            startButton = el;
          }}
        >
          <span className="startButtonText"></span>
        </div>
        </div>


        <div className="mainBox2">
          <div className="headline1">
            <h3 className="headline_text">
              <span style={{ color: "#49e594" }}></span>
              <br />
              <span style={{ color: "#49e594" }}></span>
            </h3>
          </div>
        </div>
        
        <MainSlider2/>
        <div className="mainBottom">

        </div>
      </div>
    </div>
  );
}

export default Main;
